:root {
  --color-one: #333; /* background */
  --color-two: #000; /* Black */
  --color-three: #fff; /* white */
  --color-four: #0077b5; /* blue */
  --color-five: #caccce; /* lightGrey */
  --color-six: #86888a; /* dark Grey */
}

html {
  scroll-behavior: smooth;
}
body {
  margin: 0;
  padding: 0;
  font-family: "Baumans", BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: var(--color-five);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  box-sizing: border-box;
}

.button {
  background: var(--color-three);
  color: black;
  border: 3px solid  var(--color-four);
  color: var(--color-four);
  transition: linear 0.42s all;
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  border-radius: 20px;
}

.button:hover {
  background:  var(--color-four);
  color: white;
  transition: linear 0.42s all;
  border: 3px solid  var(--color-three);
  cursor: pointer;
}

.button--circle {
  border-radius: 100%;
  height: 80px;
  width: 80px;
}

.button--right {
  visibility: visible;
  position: fixed;
  right: 20px;
  bottom: 15vh;
  z-index: 4;
  font-size: 2rem;
}
.button--right-two {
  bottom: 35vh;
}
.button--right-three {
  bottom: 55vh;
}
.button--right-four {
  bottom: 75vh;
}
.brand{
    width: 100px;
    margin: 0 auto;
}
.image {
  max-width: 100%;
  max-height: 550px;
}

.image--baner {
  width: 100vw;
}

.carousel {
  width: 90vw;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 90%;
}

.arrow {
  border: 3px solid var(--color-four);
  color: var(--color-four);
  background: var(--color-three);
  height: 200px;
  width: 75px;
  border-radius: 20px;
  margin: 0 auto;
  cursor: pointer;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 4rem;
  transition: linear 0.4s all;
}
.arrow:hover {
  background: var(--color-four);
  color: var(--color-three);
  transition: linear 0.4s all;
  border: 3px solid var(--color-three);
}
.arrow--left {
  left: 10px;
}

.arror--right {
  right: 10px;
}

.carousel--item {
  width: 60vw;
  height: 80%;
}

.carousel--item-two {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 50%;
  background: var(--color-three);
  padding: 20px;
  border-radius: 20px;
  border: 3px solid var(--color-four);
}

.carousel--item-one {
  width: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.carousel--circle {
  width: 20px;
  height: 20px;
  background: var(--color-three);
  border: 2px solid var(--color-four);
  border-radius: 50%;
  margin: 10px 20px;
  cursor: pointer;
  transition: linear 0.4s all;
}

.carousel--circle:hover {
  background: var(--color-four);
  border: 2px solid var(--color-three);
  transition: linear 0.4s all;
}

.carousel--circle--active {
  background: var(--color-four);
  border: 2px solid var(--color-three);
}

.title {
  font-size: 2.5rem;
  color: var(--color-four);
  margin: 20px auto;
}


.body{
    line-height: 2rem;
}
.icon{
    margin: 50px auto;
}
.link {
  margin: 20px auto;
  cursor: pointer;
  border: 2px solid var(--color-four);
  color: var(--color-four);
  background: var(--color-three);
  padding: 10px;
  width: 200px;
  text-align: center;
  border-radius: 20px;
  transition: linear 0.4s all;
}
.link:hover {
  background: var(--color-four);
  color: var(--color-three);
  transition: linear 0.4s all;
  border: 2px solid var(--color-three);
}

.link-active {
  background: var(--color-four);
  color: var(--color-three);
  border: 2px solid var(--color-three);
}

.section {
  display: flex;
  flex-wrap: wrap;
}

.section--column {
  flex-direction: column;
}

.section--center {
  justify-content: center;
}

.section--header {
  height: 100px;
  padding: 10px;
  position: absolute;
  width: 100%;
}

.section--footer {
  width: 100%;
  position: fixed;
  bottom: 0;
  background: var(--color-six);
  padding: 10px;
  font-size: 0.8rem;
  align-items: center;
  justify-content: center;
  opacity: 0.6;
}

.section--quote {
  height: 200px;
  align-items: center;
  justify-content: center;
  background: var(--color-three);
}

.section--portfolio {
  height: 700px;
  background: var(--color-six);
}

.section--about {
  height: 700px;
  background: var(--color-six);
}

.section--contact {
  height: 500px;
  align-items: center;
  justify-content: center;
}

.section--hero {
  width: 100%;
}

.section--h-full {
  height: 100%;
}
.section--w-full {
  width: 100%;
}
.section--w-80 {
  width: 80%;
}
.section--h-100 {
  height: 100px;
}

